* {
  box-sizing: border-box;
}
html,
body {
  font-family: 'Lantinghei SC', 'Open Sans', 'Arial', 'Hiragino Sans GB', 'Microsoft YaHei', '\\5FAE\8F6F\96C5\9ED1', 'STHeiti', 'WenQuanYi Micro Hei', 'SimSun', 'sans-serif';
  margin-bottom: 5px;
}
.deer-card {
  position: relative;
  border-radius: 4px;
  background-color: white;
  border: 1px solid #979797;
  display: flex;
  flex-direction: column;
  margin: 8px;
}
.deer-card .meta {
  box-shadow: none;
  padding: 0;
}
h3.deer-card-title {
  margin: 0;
  padding: 12px 18px;
  color: #5B6578;
  font-size: 16px;
  font-weight: 400;
  border-bottom: 1px solid #979797;
  flex: none;
  justify-content: flex-start;
}
.deer-card-content {
  padding: 18px;
  flex: auto;
}
.deer-card-footer {
  padding: 20px;
  border-top: 1px solid #979797;
  justify-content: flex-end;
}
.deer-card-dark {
  background: #5B9FD7;
}
.deer-card-dark .deer-card-title {
  color: white;
  border-bottom: 1px solid rgba(151, 151, 151, 0.4);
}
.deer-card-full {
  height: 310px;
  flex: 0 1 100%;
}
.deer-card-large {
  height: 310px;
  flex: 0 1 64%;
}
.deer-card-half {
  height: 300px;
  flex: 0 1 50%;
}
.deer-card-third {
  height: 160px;
  flex: 0 1 33%;
}
.deer-card-quarter {
  height: 180px;
  flex: 0 1 24%;
}
.deer-card-small {
  height: 160px;
  flex: 0 1 16.7%;
}
.deer-card-very-small {
  height: 84px;
  flex: 0 1 12.5%;
}
.deer-card-mini {
  height: 84px;
  flex: 0 1 8.3%;
}
.row {
  display: flex;
  flex-direction: row;
  margin-left: -10px;
}
.interval-wrapper {
  position: relative;
}
.hover-value {
  position: absolute;
  font-size: 12px;
  color: #4990E2;
  display: inline-block;
  width: 20px;
  text-align: center;
  transition: all 0.2s ease-in-out;
}
.hover-value-upper {
  margin-left: -10px;
  margin-top: 8px;
}
.hover-value-lower {
  margin-left: -10px;
  margin-top: -20px;
}
.dynamic-title {
  background: white;
  position: absolute;
  font-size: 12px;
  color: #9B9B9B;
  width: 100%;
  padding: 6px 5px;
}
.hover-active-value {
  position: absolute;
  right: 5px;
  top: -24px;
  color: #4990E2;
  font-size: 12px;
}
.hover-active-key {
  position: absolute;
  font-size: 12px;
}
.meta {
  display: flex;
  flex-direction: row;
  align-content: flex-end;
  line-height: 36px;
}
.meta-vertical {
  flex-direction: column;
}
.meta-vertical .meta-item {
  flex: 0 0 100%;
  align-self: center;
  border-left: 0 !important;
  padding-left: 0 !important;
}
.meta-item {
  flex: 1;
  align-self: flex-end;
  text-align: center;
}
.meta-item-large {
  margin-top: -6px;
  text-align: left;
}
.meta-item-large .meta-title {
  color: #9B9B9B;
}
.meta-item-large .meta-content {
  font-size: 28px;
  color: #4A4A4A;
  font-weight: 300;
}
.meta-title {
  color: #9B9B9B;
  font-size: 12px;
  margin-right: 5px;
}
.meta-content {
  color: #666;
  font-size: 20px;
}
.meta-content a {
  color: inherit;
  font-size: 12px;
  text-decoration: none;
}
.meta-content .grow {
  font-size: 18px;
  margin-left: 16px;
  position: relative;
}
.meta-content .grow::after {
  content: ' ';
  display: inline-block;
  width: 0;
  height: 0;
  position: absolute;
  bottom: 8px;
  right: -15px;
  border: 4px solid transparent;
  border-bottom-color: #9B9B9B;
}
.meta-vertical {
  line-height: 1.25;
}
.meta-vertical .meta-title {
  display: block;
}
.ac-tooltip {
  background: #fff;
  border-radius: 4px;
  padding: 8px;
  box-shadow: 0 0 4px #aaa;
  z-index: 10;
  transition: top 200ms,left 200ms;
  -moz-transition: top 200ms,left 200ms;
  /* Firefox 4 */
  -webkit-transition: top 200ms,left 200ms;
  /* Safari 和 Chrome */
  -o-transition: top 200ms,left 200ms;
}
.ac-tooltip .ac-title {
  font-size: 14px;
  padding-left: 2px;
}
.ac-tooltip .ac-list span {
  display: inline-block;
  color: #666;
}
.ac-tooltip .ac-list .ac-item-point {
  width: 6px;
  height: 6px;
  border-radius: 6px;
}
.ac-tooltip .ac-list .ac-item-text {
  margin-left: 5px;
  padding-left: 6px;
}
.title {
  box-shadow: 0.5px 0.5px 0 1px #cacaca, 0 2px 4px 0 rgba(0, 0, 0, 0.14);
  border-radius: 4px 4px 0 0;
  padding: 14px 24px;
  color: #9B9B9B;
  font-size: 14px;
}
.meta {
  box-shadow: 0.5px 0.5px 0 1px #cacaca, 0 2px 4px 0 rgba(0, 0, 0, 0.14);
  border-radius: 4px 4px 0 0;
  padding: 27px 29px;
}
.meta-vertical .meta-item:not(:nth-of-type(1))::after {
  border: 0 !important;
}
.meta .meta-item {
  text-align: left;
  position: relative;
  text-align: center;
}
.meta .meta-item:not(:nth-of-type(1))::after {
  content: ' ';
  top: 5px;
  bottom: 5px;
  width: 1px;
  border-left: 1px solid rgba(255, 255, 255, 0.3);
  border-color: inherit;
  position: absolute;
  left: 0;
}
.meta .meta-item.center {
  padding-left: 0 !important;
  text-align: center;
}
.meta .meta-title {
  display: block;
  line-height: 18px;
}
.meta .meta-content {
  color: #666666;
  font-size: 20px;
}
.percent-wrapper {
  width: 120px;
  height: 8px;
  display: flex;
  flex-direction: row;
  align-items: baseline;
}
.percent-wrapper .percent-container {
  width: 100%;
  height: 8px;
  border-radius: 8px;
  border: 1px solid #D3D0D0;
  background: #EDEDED;
  position: relative;
  box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.08) inset;
}
.percent-wrapper .percent-title,
.percent-wrapper .percent-container,
.percent-wrapper .percent-value {
  display: inline-block;
  flex: 1 1 auto;
}
.percent-wrapper-large {
  width: 300px;
}
.percent-wrapper-large .percent-value {
  color: #9B9B9B;
  font-size: 12px;
  font-weight: 300;
  flex-basis: 80px;
  text-align: right;
}
.percent-wrapper .percent-title {
  font-size: 12px;
  color: #9B9B9B;
  flex-basis: 140px;
}
.percent-wrapper .percent-content {
  position: absolute;
  min-width: 1px;
  border-radius: 8px;
  background: #9B9B9B;
  left: 0px;
  height: 8px;
  top: -1px;
}
@media (min-width: 960px) {
  .flex-container {
    width: 1208px;
    margin: 0 auto;
  }
}
@media (max-width: 960px) {
  html,
  body {
    width: 100%;
  }
  .flex-container {
    width: 100%;
    overflow-x: hidden;
  }
  .flex-container .row {
    flex-direction: column;
    margin-left: 0;
  }
  .flex-container .deer-card-full,
  .flex-container .deer-card-large,
  .flex-container .deer-card-half,
  .flex-container .deer-card-third,
  .flex-container .deer-card-quarter {
    flex: 0;
  }
  .flex-container .deer-card-small,
  .flex-container .deer-card-very-small,
  .flex-container .deer-card-mini {
    flex: 0 1 50%;
  }
}
.container {
  width: 100%;
  overflow: hidden;
  max-width: none;
  margin: 0 -1px;
}



